@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.app{
    max-width: 1200px;
    margin: 0 auto
}
a{
    text-decoration: none;
    color:#000;
    color:initial;
}
a, p, div{
    font-family: 'Roboto', sans-serif;
}

.header__logo{
  cursor: pointer;
  -webkit-animation: left-slide 2s ease-in-out ;
          animation: left-slide 2s ease-in-out ;
}
.header{
  z-index: 2;
  background: white;
  width: 100vw;
  height: 80px;
  position: fixed;
  left: 0;
  top:0;
}
.header__content{
  display: flex;
  justify-content: space-between;
margin:0 auto;
  max-width: 1200px;
  width: 100%;
  align-items: center;
  height: 80px;
}
.menu{
  display: flex;
  list-style-type: none;
  -webkit-animation: right-slide 2s ease-in-out ;
          animation: right-slide 2s ease-in-out ;
}
.menu__element{
margin-left:20px ;
}

.menu__element__link{
  font-size:22px;
}
svg.menu__burger{
  display: none;

}
.overlay{
display:none;
}
@media (max-width: 1000px) {
  .header__logo{
    -webkit-animation: fall-left 1s ease-in-out ;
            animation: fall-left 1s ease-in-out ;
  }
  .header__content{

    width: calc(100% - 40px);
  }
  svg.menu__burger--open{
    -webkit-animation: fall-right 1s ease-in-out ;
            animation: fall-right 1s ease-in-out ;
  }
  svg.menu__burger--close{
    fill:white;
    -webkit-animation: rotate 1s ease-in-out 0.5s;
            animation: rotate 1s ease-in-out 0.5s;
  }
  svg.menu__burger{
    display: inline;
    display: initial;
    z-index:30;
    font-size:2rem;
    
  }
  .menu{
flex-direction: column;
z-index: 30;
-webkit-animation: none;
        animation: none;
padding: 0;
position: fixed;
top: 120px;
  }
  .menu--active{
    flex-direction: column;
    z-index: 30;
    -webkit-animation: none;
            animation: none;
    padding: 0;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    align-items: center;
      }
      .menu--inactive{
   display:none;
          }


  .menu__element{
    margin-bottom:48px ;
    z-index: 40;
margin-left:0 ;

    }
    .menu__element__link{
     color:white;
    }
    .overlay--active{
      position: fixed;
      background-color:rgba(0, 0, 0, 0.9);
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: 3;
      -webkit-animation: slide 0.5s ease-in-out ;
              animation: slide 0.5s ease-in-out ;
  }

}
@-webkit-keyframes  right-slide {
  0% {
    transform: translateX(200px) rotate(0);
    opacity: 0;

  }
  40% {
    opacity: 1;
  }
  80% {
    transform: translateX(-20px) rotate(-10deg);
  }
  100% {
    transform: translateX(0) rotate(0);
  }
}
@keyframes  right-slide {
  0% {
    transform: translateX(200px) rotate(0);
    opacity: 0;

  }
  40% {
    opacity: 1;
  }
  80% {
    transform: translateX(-20px) rotate(-10deg);
  }
  100% {
    transform: translateX(0) rotate(0);
  }
}
@-webkit-keyframes left-slide {
  0% {
    transform: translateX(-200px) rotate(0);
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    transform: translateX(20px) rotate(10deg);
  }
  100% {
    transform: translateX(0) rotate(0);
  }
}
@keyframes left-slide {
  0% {
    transform: translateX(-200px) rotate(0);
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    transform: translateX(20px) rotate(10deg);
  }
  100% {
    transform: translateX(0) rotate(0);
  }
}
@-webkit-keyframes  fall-left{
  0% {
    transform: translateY(-40px) rotate(0);

  }

  40% {
    transform: translateY(8px) rotate(8deg);
  }
  70% {
    transform: translateY(-4px) rotate(-4deg);
  }
  100% {
    transform: translateY(0) rotate(0);
  }
}
@keyframes  fall-left{
  0% {
    transform: translateY(-40px) rotate(0);

  }

  40% {
    transform: translateY(8px) rotate(8deg);
  }
  70% {
    transform: translateY(-4px) rotate(-4deg);
  }
  100% {
    transform: translateY(0) rotate(0);
  }
}
@-webkit-keyframes  rotate{
  0% {
    transform:   rotate(0) scale(0.2);

  }

  100% {
    transform:   rotate(900deg) scale(1);

  }
}
@keyframes  rotate{
  0% {
    transform:   rotate(0) scale(0.2);

  }

  100% {
    transform:   rotate(900deg) scale(1);

  }
}
@-webkit-keyframes  fall-right{
  0% {
    transform: translateY(-40px) rotate(0);

  }

  40% {
    transform: translateY(8px) rotate(-8deg);
  }
  70% {
    transform: translateY(-4px) rotate(4deg);
  }
  100% {
    transform: translateY(0) rotate(0);
  }
}
@keyframes  fall-right{
  0% {
    transform: translateY(-40px) rotate(0);

  }

  40% {
    transform: translateY(8px) rotate(-8deg);
  }
  70% {
    transform: translateY(-4px) rotate(4deg);
  }
  100% {
    transform: translateY(0) rotate(0);
  }
}
@-webkit-keyframes   slide {
  0% {
    transform: translateX(-600px) ;

  }

  100% {
    transform: translateX(0) ;
  }
}
@keyframes   slide {
  0% {
    transform: translateX(-600px) ;

  }

  100% {
    transform: translateX(0) ;
  }
}
.banner{
    position: absolute;
    left: 0;
    top: 80px;
width:100%;
height: calc(100vh - 80px);
overflow: hidden;
background-image:url(/Youngmedia/static/media/motto-bg.726f5726.jpg);
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
-webkit-animation:banner-rollout ease-in-out 1.5s;
        animation:banner-rollout ease-in-out 1.5s;
}
.banner__img{
width:100%;
}
.banner__quote{
    font-size: 70px;
    color: #fff;
    text-transform: uppercase;
    font-family: 'Raleway', sans-serif;
    font-weight: 300;
    letter-spacing: -2px;
    text-align: center;
  }
  .banner__author{
    font-style: italic;
    text-align: right;
    color:white;
  }
  .banner__quote{
  

  }
  @media (max-width: 1000px) {
    .banner__quote{
      font-size: 40px;
  
    }
    .banner{
      background-size: cover;
      background-position: center center;
    }
  }
  @-webkit-keyframes banner-rollout {
    0% {
      max-height: 0;
    }
    100% {
      max-height: 1000px;

    }
  }
  @keyframes banner-rollout {
    0% {
      max-height: 0;
    }
    100% {
      max-height: 1000px;

    }
  }

